import React from "react";
import { Bead, Editor, ObjectTypes } from "../editorObjects/Editor";
import { useSelector } from "react-redux";
import styled from "styled-components";
import { sendEvent } from "../controllers/storeController";
import {
  AppEventType,
  ChangeUiSettingsSidebar,
} from "../controllers/AppEventType";

export const BeadsSummary: React.FC<{}> = () => {
  const { groupedCount } = useSelector((state: Editor) => {
    const temp = {};
    Object.keys(state.objects).forEach((id) => {
      if (state.objects[id].objectType === ObjectTypes.Bead) {
        const bead = state.objects[id] as Bead;
        if (temp[bead.color] === undefined) {
          temp[bead.color] = 0;
        } else {
          temp[bead.color] = temp[bead.color] + 1;
        }
      }
    });
    const groupedCount: {
      name: string;
      value: string;
      color: string;
      textName: string;
      textTooltip: string;
      textCount: string;
    }[] = [];
    const maxGroups = 10;
    Object.keys(temp).forEach((color, index) => {
      if (index > maxGroups) {
        return;
      }
      const textName = color.toString();
      const textCount = `${temp[color]}x`;
      const textTooltip = `${color} - ${temp[color]}`;
      groupedCount.push({
        name: color,
        value: temp[color],
        color,
        textName,
        textTooltip,
        textCount,
      });
    });
    return {
      groupedCount,
    };
  });
  const onClickOpenObjectsPanel = () => {
    const event: ChangeUiSettingsSidebar = {
      name: AppEventType.ChangeUiSettingsSidebar,
      payload: {
        panel: "objects",
        open: true,
        toggle: true,
      },
    };
    sendEvent(event);
  };
  return (
    <BeadsSummaryStyles>
      <div className={"beads-summary"}>
        <div
          className={"beads-summary-content"}
          onClick={onClickOpenObjectsPanel}
        >
          {groupedCount.map((obj, index) => {
            return (
              <div
                className={"bead-count"}
                key={index}
                // @TRANSLATION
                data-tip={obj.textTooltip}
                data-place={"top"}
                data-type={"dark"}
                data-effect={"solid"}
              >
                <span
                  className={"preview"}
                  style={{ backgroundColor: obj.color }}
                >
                  {obj.textName}
                </span>
                <span className={"value"}>{obj.textCount}</span>
              </div>
            );
          })}
        </div>
      </div>
    </BeadsSummaryStyles>
  );
};

export const BeadsSummaryStyles = styled.div`
  .beads-summary {
    position: absolute;
    bottom: var(--standardPadding);
    right: calc(var(--standardPadding) + 170px + var(--standardPadding));
    z-index: var(--zIndexBeadsSummary);
    transition: all 0.4s ease-in-out;
  }
  .beads-summary-content {
    background: #fff;
    display: flex;
    column-gap: var(--standardPadding);
    height: 55px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-left: calc(var(--standardPadding) * 2);
    padding-right: calc(var(--standardPadding) * 2);
  }
  .bead-count {
    display: flex;
    flex-direction: column;
    cursor: pointer;

    .value {
      font-size: 15px;
      font-weight: 600;
    }

    .preview {
      width: 30px;
      height: 30px;
      border: 1px solid #eee;
      border-radius: 30px;
      font-size: 8px;
      line-height: 27px;
      display: flex;
      justify-content: center;
      align-items: center;
      white-space: nowrap;
      overflow: hidden;
    }
  }
`;
